/*=====================
 button css start
==========================*/
button {
  cursor: pointer;
}

.btn {
  line-height: 20px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  border-radius: 0;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.btn:hover {
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.btn:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn-solid {
  padding: 13px 29px;
  color: #ffffff;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult);
  background-image: linear-gradient(
    30deg,
    var(--theme-deafult) 50%,
    transparent 50%
  );
  background-size: 850px;
  background-repeat: no-repeat;
  background-position: 0;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
}
.btn-solid:hover {
  background-position: 100%;
  color: #000000;
  background-color: #ffffff;
}
.btn-solid.black-btn {
  background-image: linear-gradient(30deg, #222222 50%, transparent 50%);
  border: 2px solid #222222;
}
.btn-solid:focus {
  color: #000000;
}
.btn-solid.btn-gradient {
  background: var(--theme-deafult);
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(var(--theme-deafult)),
    color-stop(var(--theme-deafult2)),
    color-stop(var(--theme-deafult)),
    to(var(--theme-deafult2))
  );
  background-image: linear-gradient(
    to right,
    var(--theme-deafult),
    var(--theme-deafult2),
    var(--theme-deafult),
    var(--theme-deafult2)
  );
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background-size: 300% 100%;
  border: none;
}
.btn-solid.btn-gradient:hover {
  background-position: 100% 0;
  color: white;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.btn-solid.btn-green {
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(var(--theme-deafult)),
    to(var(--theme-deafult2))
  );
  background-image: linear-gradient(
    to right,
    var(--theme-deafult),
    var(--theme-deafult2)
  );
  border: none;
  background-color: var(--theme-deafult);
}
.btn-solid.btn-green:hover {
  background-color: var(--theme-deafult);
  background-image: none;
  color: white;
}
.btn-solid.btn-sm {
  padding: 9px 16px;
}
.btn-solid.btn-xs {
  padding: 5px 8px;
  text-transform: capitalize;
}
.btn-solid.hover-solid {
  -webkit-transition: none;
  transition: none;
}
.btn-solid.hover-solid:hover,
.btn-solid.hover-solid:focus {
  background-color: var(--theme-deafult);
  opacity: 0.92;
  color: #ffffff;
  -webkit-transition: none;
  transition: none;
}

.btn-outline {
  display: inline-block;
  padding: 13px 29px;
  letter-spacing: 0.05em;
  border: 2px solid var(--theme-deafult);
  position: relative;
  color: #000000;
}
.btn-outline:before {
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--theme-deafult);
  z-index: -2;
}
.btn-outline:hover,
.btn-outline:focus {
  color: #ffffff !important;
}
.btn-outline:hover i,
.btn-outline:focus i {
  color: #ffffff !important;
}
.btn-outline:hover:before,
.btn-outline:focus:before {
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
}
.btn-outline.btn-sm {
  padding: 9px 16px;
  font-size: 12px;
}

.btn-rounded {
  border-radius: 30px;
}

button.btn.btn-solid:active,
button.btn.btn-outline:active {
  background-image: linear-gradient(
    30deg,
    var(--theme-deafult) 50%,
    transparent 50%
  );
  color: #ffffff;
  background: var(--theme-deafult);
}

.btn-classic:hover {
  background-color: var(--theme-deafult);
}

