  background-color: white;
  color: var(--theme-deafult);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  padding: 10px 29px;
}
.btn-white:hover {
  color: #000000;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-close {
  background: none;
}

.btn-custom {
  padding: 6px 30px !important;
  border: none;
}

.btn-animation {
  position: relative;
  overflow: hidden;
}
