.christmas-btn {
  display: inline-block;
  min-width: 100px;
  padding: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320))) 33px;
  font-size: calc(18px + (26 - 18) * ((100vw - 320px) / (1920 - 320)));
  line-height: 26px;
  text-decoration: none;
  color: #fff !important;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.75);
  background: #e56c6c;
  outline: none;
  border-radius: 15px;
